    'Part 90':'9. Conclusion',
    'Part 91':'This is the goal of Violas: to create a global ecosystem of stablecoins that enable local fiat currency and local financial applications to adapt Libras blockchain technology in an effort to serve billions of people in everyday transactions. All local digital currency issued from Violas are backed by an equal amount of its corresponding local fiat currency. Consequently, a local digital currency can be used equally to its local fiat currency without any additional reference. This is fiat currency in the form of cryptocurrency with the modern advancements of safety, security, transparency, real-time transaction speed, audited reserve, and low cost of use built in.',
    'Part 92':'Violas also encourages competition among local digital currencies. We believe more advanced local digital currencies will emerge from competition through the process of natural selection in accordance with Friedrich A. Hayeks competing currency theory.',
